function AboutMe({ isThemeDark, isEnlishVersion }) {
  return (
    <section className="about-me">
      <div className="about-me__container">
        <h2 className="about-me__title">
          {isEnlishVersion ? "About me" : " "}
        </h2>
        <div className="about-me__text-container">
          <p className="about-me__text">
            {isEnlishVersion
              ? " Im 21 years old and Im a beginner Frontend developer from Moscow, looking for interesting tasks and interested in development"
              : "  21     Frontend-  ,       "}
          </p>
          <p className="about-me__text">
            {isEnlishVersion
              ? " I came to development because I am interested in learning and creating something new, improving the user experience."
              : "           - ,   "}
          </p>
          <p className="about-me__text">
            {isEnlishVersion
              ? " I study on the platform "
              : "    "}
            <a
              className={`${
                isThemeDark
                  ? "about-me__link about-me__link_white"
                  : "about-me__link"
              }`}
              href="https://practicum.yandex.ru/"
              target="_blank"
              rel="noreferrer"
            >
              {isEnlishVersion
                ? "Yandex Practicum (Web-development)"
                : "  (Web-)"}
            </a>
            {isEnlishVersion
              ? ", at the same time I am studying interesting technologies and doing pet projects, actively looking for work"
              : ",       pet-,    "}
          </p>
        </div>
      </div>
    </section>
  );
}

export default AboutMe;
